.btn {
  display: inline-flex;
  justify-content: center;
  font-family: "GothamProMedium", sans-serif;
  text-transform: uppercase;
  color: #fff;
  padding: 14px 35px;
  border: solid 2px #fff;
  background: transparent;
  cursor: pointer;
  outline: none;
  overflow: hidden;
  position: relative;
  font-size: 14px;
  transition: all 0.3s linear 0s;
  line-height: 1 !important;
  text-decoration: none;
  text-align: center;
}

.btn.default-btn {
  text-decoration: none;
}

.btn.default-btn:hover {
  background: #fff;
  color: #000;
  text-decoration: none;
}

.btn.red-border-btn {
  border-color: #d4311c;
  color: #000;
}

.btn.red-border-btn:hover {
  background: #d4311c;
  color: #fff;
}

.btn.filled-red-btn {
  background: #d4311c;
  color: #fff;
}

.btn.filled-red-btn:hover {
  background: none;
  color: #000000;
}

.btn.white-border-btn {
  border-color: #fff;
  color: #fff;
}

.btn.white-border-btn:hover {
  background: #fff;
  color: #000;
}

.btn.filled-white-btn {
  background: #d4311c;
  color: #fff;
}

.btn.filled-white-btn:hover {
  background: none;
  color: #000000;
}

.btn.back-catalog {
  padding: 14px 30px;
}

.btn.back-catalog:hover svg {
  fill: #fff;
}

.btn.back-catalog svg {
  fill: #d4311c;
  font-size: 14px;
  margin-right: 15px;
  transition: all 0.3s linear 0s;
  vertical-align: middle;
}

button.btn,
input.btn {
  display: inline-block;
}

html {
  box-sizing: border-box;
  font-family: sans-serif;
  line-height: 1.5;
  -webkit-text-size-adjust: 100%;
