.custom-checkbox {
  position: relative;
  margin: 30px 0;
}

.custom-checkbox input {
  position: absolute;
  right: -999999px;
  z-index: -999999;
  display: none;
}

.custom-checkbox input:checked + label span:after {
  visibility: visible;
}

.custom-checkbox label {
  font-size: 14px;
  font-weight: normal;
  padding-left: 45px;
  min-height: 30px;
  display: inline-flex;
  align-items: center;
  line-height: 1.2;
}

@media (min-width: 568px) {
  .custom-checkbox label {
    font-size: 16px;
  }
}

.custom-checkbox label span {
  display: block;
}

.custom-checkbox label span:before {
  content: '';
  width: 30px;
  height: 30px;
  display: block;
  border: solid 2px #d4311c;
  position: absolute;
  left: 0;
  top: 0;
}

.custom-checkbox label span:after {
  content: '';
  width: 30px;
  height: 30px;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  background: url("../img/check-ico.png") no-repeat center center;
  visibility: hidden;
}

input.error,
textarea.error {
  border: solid 2px #d4311c;
}

div.error,
label.error {
  position: absolute;
  background: #fff;
  z-index: 1;
  right: 0;
  top: 104%;
  line-height: 1.2;
  font-size: 12px;
  color: #d4311c;
  font-family: "GothamProMedium", sans-serif;
  border: solid 1px #d4311c;
  padding: 13px 20px;
}

@media (min-width: 568px) {
  div.error,
  label.error {
    font-size: 14px;
  }
}

div.error:before, div.error:after,
label.error:before,
label.error:after {
  content: '';
  position: absolute;
  left: 10px;
  top: -14px;
  border: 7px solid transparent;
  border-bottom: 7px solid #d4311c;
}

div.error:after,
label.error:after {
  border-bottom: 7px solid #fff;
  top: -13px;
}

